<template>
  <div class="main-wrapper">    
    <MainHeader/>
    <!-- <div class="attention-work-beginning">Начнет работу в понедельник, 20.02.2023 в 8:00</div> -->
    <div class="login-modal-wrapper active">
      
      <div class="login-modal">
        <div class="login">Вход</div>
        <div class="login-form">
          <div class="input-wrapper">
            <div class="input-description">Логин:</div>
            <input
              v-model="loginPassword.login"
              type="text"
              class="login-input">
          </div>
          <div class="input-wrapper">
            <div class="input-description">Пароль:</div>
            <input
              v-model="loginPassword.password"
              type="password"
              class="login-input">
          </div>
        </div>
        <div class="btn rounded-btn" @click="logIn">Войти</div>
      </div>
    </div>
    <!-- <p class="login-description">Забыли пароль для входа? Обратитесь к своему руководителю (?)</p> -->
    <LoginWarning
          v-if="isLoginWarning"
        />
  </div>
</template>

<script>
  import {mapGetters, mapActions} from 'vuex'
  import LoginWarning from '@/components/LoginWarning.vue'
  import MainHeader from '@/components/MainHeader.vue'
  
  export default {
    data() {
      return {
        // loginPassword: {
        //   "login": "user-11",
        //   "password": "userpassword1"
        // }
        loginPassword: {
          "login": "",
          "password": ""
        }
      }
    },
    components: {
      LoginWarning,
      MainHeader      
    },
    computed: {
      ...mapGetters([
        'isLoginWarning'
      ]),
    },
    methods: {
      ...mapActions([
        'SIGN_IN',
      ]),
      logIn() {
        this.SIGN_IN(this.loginPassword)
      }
    }
  }
</script>

<style lang="scss">
  .attention-work-beginning{
    font-family: 'Open Sans';
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 100%;
    text-align: center;
    color: #D50000;
    margin-top: 100px;
  }
</style>