<template>
  <div class="body-answer">
    <p class="answer-header">Выберите верный ответ:</p>

    <div class="answers-container"
      v-if="answers.type==='text'"
    >
      <div
        v-for="answer in answers.items"
        :key="answer.id"
      >
        <input type="radio" :id="answer.id" :value="answer.id" v-model="picked" />
        <label :for="answer.id">{{answer.text}}</label>

      </div>
    </div>

    <div
      v-if="answers.type==='image'"
      class="answer-image-list"
    >
      <div
        v-for="answer in answers.items"
        :key="answer.text"
      >
        <div class="answer-image-item">

          <input type="radio" :id="answer.id" :value="answer.id" v-model="picked" />
          <label :for="answer.id">
            <div class="img-container">
              <img :src="questionImage(answer.image)" alt="image" class="body-image">
            </div>
          </label>

        </div>

      </div>

    </div>

    <!-- <br />
      <span>Выбрано: {{ picked }}</span>
      <span>Выбрано: {{ isRightAnswer }}</span>

    <br><br><br> -->

  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  data() {
    return {
      picked: ""
    }
  },
  props:{
    answers: {}
  },
  computed:{
    ...mapGetters([
      'currentAnswerId'
    ]),
    
    isRightAnswer(){
      return this.answers.items.find(x => x.id === this.picked)?.isRight
    }
    
  },
  methods: {
    ...mapActions([
        'setCurrentAnswerId'
    ]),
    questionImage(answerImage){
      return require(`@/assets/images/${answerImage}`)
    }
  },
  watch:{
    picked(newPick){
      this.setCurrentAnswerId(newPick)
    },
    currentAnswerId(newAnswerId){
      if (newAnswerId === "") {
        this.picked = ""
      }
    }
  },


}
</script>

